/// --------------------------------------------------------------
/// Customized version of Foundation's _global.scss file in order
/// to reset internals to suit preference.
/// --------------------------------------------------------------
/// NOTE: Globally defined variables have moved to settings.scss
/// --------------------------------------------------------------


